import { mapGetters, mapActions } from 'vuex'
import { systemConfig } from 'common/js/config'

const debug = process.env.NODE_ENV !== 'production'

export default {
  props: {
    loadStart: {
      type: Boolean,
      default: false
    },
    KeyWord: {
      type: String,
      default: ''
    }
  },
  computed: {
    ...mapGetters([
      'routerParams'
    ])
  },
  data () {
    return {
      ReportData: [],
      fileLink: '',
      offset: 100
    }
  },
  methods: {
    // 获取报表数据
    GetReport () {
      let FormId = this.routerParams.FromId
      let where = ''

      this.GetReportData({FormId, where}).then((res) => {
        let value = res.data.value
        if (value) {
          this.ReportData = [...JSON.parse(value)]
        } else {
          this.ReportData = []
        }
      }).catch((e) => {
        this.AlertShowEvent(e.message)
      })
    },
    // 打开报表
    openReport (item) {
      let KeyValue = this.routerParams.Id
      let host = debug ? systemConfig.devDomainName : ''
      let link = `${host}/PowerPlat/FormXml/ReportView.aspx?rid=${item.Id}&KeyWord=${this.KeyWord}&KeyValue=${KeyValue}`

      this.fileLink = link
      this.offset = 0
    },
    // 关闭文件
    closeFile () {
      this.offset = 100
      if (this.timer) {
        clearTimeout(this.timer)
      }

      this.timer = setTimeout(() => {
        this.fileLink = ''
      }, 400)
    },
    ...mapActions([
      'GetReportData',
      'AlertShowEvent'
    ])
  },
  destroyed () {
    clearTimeout(this.timer)
  },
  watch: {
    loadStart: {
      handler (newLoad, oldLoad) {
        if (this.loadStart) {
          this.GetReport()
        }
      },
      immediate: true
    }
  }
}



